<template>
    <el-carousel  type="card"  height="450px" :interval="4000">
      <el-carousel-item v-for="(item,index) in pictureList" :key="index">
         <el-image :src="require(`../assets/${item.image}.jpg`)" fit="fit" @click.native="handleViewImage(`../assets/${item.image}.jpg`)"></el-image>
      </el-carousel-item>
    </el-carousel>
</template>

<script>
export default {
  name: "pictureList",
  props:{
     pictureList: { required: true, type: Array}
  },
  methods:{
    handleViewImage(){
      console.log()
    }
  }
};
</script>




