<template>
  <div>
    <!-- <el-tag v-for="(item,index) in circleArray" :key="index" :style="{ left: item.x + 'px', right: item.y + 'px', position: 'absolute'}"></el-tag> -->
    <div id="canvas"></div>
  </div>
</template>

<script>
export default {
   name: "Sample",
   data(){
     return{
       circleArray:[]
     }
   },
   methods:{
     createNodes(numNodes, radius){
       var nodes = [],
             width = (radius * 2) + 50,
             height = (radius * 2) + 50,
             angle,
             x,
             y,
             i;
         for (i=0; i<numNodes; i++) {
            angle = (i / (numNodes/2)) * Math.PI;
            x = (radius * Math.cos(angle)) + (width/2);
            y = (radius * Math.sin(angle)) + (width/2);
            nodes.push({'id': i, 'x': x, 'y': y});
         }
         return nodes;
     }
   },
   mounted(){
      this.circleArray = this.createNodes(100,100);
      console.log(this.circleArray)
   }
}
</script>



